.profile {
   .avatar__box {
      padding: 40px;
      .avatar {
         border-radius: 10px; } }

   .name {
      color: #FFF;
      font-size: 32px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin: 20px 0; }
   .box {
      border-radius: 30px;
      background: #1B2732;
      padding: 40px;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25); }

   .status {
      color: #FFF;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      input {
         color: black; } }
   .edit {
      button {
         border-radius: 50px;
         border: 1px solid #D5D5D5;
         background: rgba(217, 217, 217, 0.00);
         font-family: Montserrat;
         font-size: 18px;
         font-style: normal;
         font-weight: 400;
         line-height: normal;
         margin: 0 10px;
         margin-top: 80px;
         padding: 10px 25px;
         color: #FFF; } }
   .desc {
      color: #FFF;
      font-family: Montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal; }
   .job {
      color: #FFF;
      margin: 20px 0;
      font-family: Montserrat;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: normal; }
   .links {
      border-radius: 30px;
      background: #1B2732;
      margin: 50px 0;
      padding: 40px;
      color: white; }
   .wall {
      display: flex;
      .post {
         border-radius: 30px;
         background: #1B2732;
         margin: 50px 0;
         margin-left: 50px;
         padding: 40px;
         color: white;
         width: 100%; } } }

